<template>
  <div id="addRoom">
    <head-top title="Add Rooms" goBack="true"></head-top>
    <div class="content">
      <span>Please name your room</span>
      <div class="editorName" :class="borderColor? 'active_color':''">
        <input type="text" placeholder="e.g.Growroom, Bedroom"
        v-model="nameStr" maxlength="16"
        @focus="borderColor = !borderColor"
        @blur="borderColor = !borderColor"
        @input="nameExp(nameStr)" />
      </div>
      <span :style="{color:toastFalg? 'red': ''}">Tip：2-16 characters,only letters and numbers allowed</span>
      <span>The room has already existed. Please reenter the name of the room</span>
      <div class="list">
        <span>Select the device you want to add</span>
        <ul v-show="listData.length > 0">
          <li v-for="item in listData" :key="item.id">
            <div class="left">
              <img :src="item.icon" alt="">
              <span>{{item.name}}</span>
            </div>
            <div class="right" @click="tabswitch(item)">
              <img src="../../assets/addevice/click@3x.png" alt="" v-if="item.status">
              <img src="../../assets/addevice/defunct@3x.png" alt="" v-if="!item.status">
            </div>
          </li>
        </ul>
        <div class="noList" v-if="listData.length === 0">
          No devices
        </div>
      </div>
      <div class="nextbtn" @click="Finished" :class="nameStr === ''? 'disabClick' : ''">
        <span>Finished</span>
      </div>
    </div>
  </div>
</template>
<script>
import headTop from '../../components/common/head'
import { getStore, setStore } from '../../utils'
// import {
//   addRoomRequest,
//   unbundlingDeviceRequest
// } from '../../api/method'
import {
  addRoomResponse
} from '../../api/getData'
import { mapActions, mapGetters } from 'vuex'
// const heartIMG = require('../../assets/device/heater@3x.png')
// const humideyIMG = require('../../assets/device/humidier@3x.png')
// const lightIMG = require('../../assets/device/light@3x.png')
export default {
  data () {
    return {
      borderColor: false, // input 获取焦点时border颜色
      nameStr: '', // 输入内容
      toastFalg: false, // 输入格式错误提示
      fullHeight: null, // 当前屏幕高度
      listData: null // 设备列表
    }
  },
  components: {
    headTop
  },
  computed: {
    ...mapGetters(['getDevicelist', 'getDevicesdata']),
    deviceList () {
      return this.getDevicelist
    },
    devicesData () {
      return this.getDevicesdata
    }
  },
  created () {
    this.data_init()
  },
  mounted () {
    console.log(this.devicesData)
    // 添加房间返回
    addRoomResponse(this.NAVIGATOR, this)
    this.fullHeight = document.documentElement.clientHeight
    // this.listData = [
    //   {
    //     icon: heartIMG,
    //     type: 'Heater',
    //     status: true
    //   }
    // ]
  },
  methods: {
    ...mapActions(['setRoomdata', 'setDevicelist']),
    data_init () {
      this.listData = this.deviceList
      this.listData.forEach(element => {
        element.status = false
      })
    },
    nameExp (val) {
      // 输入内容格式验证
      if (!/^\w{0,}$/.test(val)) {
        this.toastFalg = true
      } else {
        this.toastFalg = false
      }
    },
    tabswitch (item) {
      console.log(item.status)
      let flag = item.status
      // 视图不会刷新解决办法
      this.$delete(item, 'status')
      this.$set(item, 'status', !flag)
    },
    Finished () {
      if (this.toastFalg || this.nameStr === '') {
        return false // 房间名格式错误
      }
      let macs = ''
      let icons = []
      if (this.listData.length > 0) {
        console.log(this.listData)
        this.listData.forEach(element => {
          if (element.status) {
            console.log(element)
            macs += '&' + element.mac
            icons.push(element.mode)
          }
        })
      }
      console.log(icons)
      let id
      let roomData = JSON.parse(getStore('roomData'))
      if (roomData) {
        id = roomData.length
        for (let i in roomData) {
          if (this.nameStr === roomData[i].name) {
            this.$store.state.ToastType = 'sameRoom'
            return false
          }
        }
      } else {
        id = 0
        roomData = []
      }
      if (this.nameStr) {

      }
      let obj = { // 房间
        name: this.nameStr,
        id: id,
        mac: macs.slice(1),
        icon: icons
      }
      roomData.push(obj)
      setStore('roomData', roomData)
      let macArray = macs.slice(1)
      macArray = macArray.split('&')
      this.setRoomdata(roomData)
      let newdeviceList = this.deviceList
      if (newdeviceList.length) {
        newdeviceList = newdeviceList.filter(item => {
          if (macArray.indexOf(item.mac) < 0) {
            return item
          }
        })
      }
      this.setDevicelist(newdeviceList)
      // addRoomRequest(this.NAVIGATOR, this, this.nameStr, macs.slice(1))
      this.$router.push({path: '/Me'})
    }
  }
}
</script>
<style lang="less" scoped>
#addRoom{
  .wh(100%, 100%);
  .fs;
  z-index: 999;
  background: white;
  display: flex;
  flex-direction: column;
  .editorName {
    padding: 0 30px;
    .wh-l(100%, 100px);
    border-top: 1px solid #cccccc;
    border-bottom: 1px solid #cccccc;
    display: flex;
    justify-content: space-between;
    input {
      flex: 1;
      font-size: 24px;
      background-color: transparent;
      .wh-l(auto, 100%);
      color: black;
      font-weight: 600px;
    }
    input::-webkit-input-placeholder {
      /* WebKit browsers */
      color: #cccccc;
    }
    img {
      .wh(24px, 24px);
      align-self: center;
    }
  }
  .content{
    display: flex;
    flex-direction: column;
    span{
      padding: 0 30px;
      font-size: 25px;
      line-height: 40px;
      text-align: left;
    }
    span:nth-child(1) {
      font-size: 32px;
      line-height: 92px;
      text-align: left;
      .wh(100%, 92px);
    }
    span:nth-of-type(2){
      margin-top: 20px;
      color: #dadada;
    }
    span:nth-of-type(3){
      color: #dadada;
      // color: #fd403e;

    }
    .list{
      display: flex;
      flex-direction: column;
      span{
        line-height: 110px;
        height: 110px;
      }
      ul{
        li{
          display: flex;
          justify-content: space-between;
          // justify-content: space-between;
          .wh(100%, 93px);
          border-top: 1px solid #dadada;
          padding-left: 30px;
          line-height: 93px;
          .left{
            img{
              .wh(23px,38px);
              margin-right: 30px;
              align-self: center;
            }
            display: flex;
            span{
              font-size: 30px;
              color: #575757;
              line-height: 93px;
            }
          }
          .right{
            display: flex;
            width: 10%;
            justify-content: flex-end;
            padding-right: 30px;
            img{
              .wh(40px,40px);
              align-self: center;
              margin-left: 30px;
            }
          }
        }
        li:last-child {
          border-bottom: 1px solid #dadada;
        }
      }
      .noList{
        border-bottom: 1px solid #dadada;
        border-top: 1px solid #dadada;
        .wh-l(100%, 300px);
        font-size: 25px;
      }
    }
  }
  .active_color {
    border-color: #01a29a;
  }
  .nextbtn{
    .wh-l(520px,100px);
    margin: 0 auto;
    border-radius: 10px;
    background: #13af68;
    margin-top: 120px;
    span{
      font-size: 47px;
      color: #fff;
      vertical-align: middle;
    }
  }
  .disabClick {
    background: #D6D6D6;
    opacity: 0.7;
  }
}
</style>



